import React from "react";
import { graphql, Link } from "gatsby";
import styled from "styled-components";
import Layout from "../components/layout";
import { respondTo } from "../components/shared/_respondTo";

const BlogContainer = styled.div``;

const BlogPost = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 1024px;
  margin: 0 auto;
  ${respondTo.xs`
    width:100%;
  `}
`;

const BlogHeader = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 3rem 0;
`;

const BlogTitle = styled.h1`
  ${respondTo.xs`
    font-size:1.5rem;
  `}
`;

const BlogDate = styled.h5``;

const BlogPostContent = styled.div`
  min-width: 960px;
  ${respondTo.xs`
    font-weight:500;
    min-width:0;
    width:100%;
    blockquote{ 
      width:100%;
      margin:0;
      padding:1.5rem 2rem;
      line-height:1.7;
      word-break: keep-all;
      overflow-wrap: break-word;
    }
    table{
      display:block;
      width:100%;
      overflow-x:scroll;
      *{
        // white-space:nowrap;
      }
    }
  `}
`;

const BlogPagination = styled.div`
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 3rem;
`;

export default function Template({
  data,
  pageContext, // this prop will be injected by the GraphQL query below.
}) {
  const { markdownRemark } = data; // data.markdownRemark holds your post data
  const { frontmatter, html } = markdownRemark;
  const { next, previous } = pageContext;

  const checkMobile = () => {
    return window.outerWidth <= 480 ? true : false;
  };

  const matchSquareBracket = () => {
    const regExp = /\[(.*?)\]/;
    if (checkMobile) {
      const word = frontmatter.title.match(regExp);

      if (word) {
        const others = frontmatter.title.replace(word[0], "");
        return { word: word[0], others };
      } else return { word: null, others: frontmatter.title };
    } else {
      return frontmatter.title;
    }
  };
  return (
    <Layout>
      <BlogContainer>
        <BlogPost>
          <BlogHeader>
            <BlogTitle>
              {matchSquareBracket() ? (
                matchSquareBracket().word ? (
                  <>
                    <div>{matchSquareBracket().word}</div>
                    <div>{matchSquareBracket().others}</div>
                  </>
                ) : (
                  matchSquareBracket().others
                )
              ) : (
                matchSquareBracket()
              )}
            </BlogTitle>
            <BlogDate>{frontmatter.date}</BlogDate>
          </BlogHeader>
          <BlogPostContent dangerouslySetInnerHTML={{ __html: html }} />
          <BlogPagination>
            {previous ? (
              <Link to={previous.frontmatter.path}>
                {`<- ${previous.frontmatter.title}`}
              </Link>
            ) : (
              <span></span>
            )}
            {next ? (
              <Link
                to={next.frontmatter.path}
              >{`${next.frontmatter.title} ->`}</Link>
            ) : (
              <span></span>
            )}
          </BlogPagination>
        </BlogPost>
      </BlogContainer>
    </Layout>
  );
}

export const pageQuery = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        date(formatString: "YYYY년 MM월 DD일, HH:mm")
      }
    }
  }
`;
